<template>
    <div>
        <navbar title="人民币充值提现USDT" path="/mine" v-if="$phonemodel == 'Android'"></navbar>
        <div class="app" :class="{nav:$phonemodel == 'Android'}">
            <div class="iframe">
                <iframe :src="url" frameborder="0" scrolling="no" width="100%" height="100%"></iframe>
            </div>
        </div>
    </div>
</template>

<script>
    import navbar from "@/components/navbar";
    export default {
        name: "ThirdRe",
        components:{
            navbar
        },
        data(){
            return {
                url:'',
            }
        },
        created()
        {
            let address = this.$route.query.address
            console.log(address)
            let url = 'https://buy.flashbuy.io/#/trade?name=usdt-erc20&cny=0&address='+address
            this.url = url;
        }

    }
</script>

<style scoped>

    .app {
        height: calc(100vh - 0.44rem);
        padding-bottom: 0;
    }
    .app .iframe {
        margin-top: 0.44rem;
        height: 100%;
    }
</style>



